@import '../../../../variables';
.face {
  background-image: url(../../../../assets/sprite.png);
  width: 25px;
  height: 25px;
  &__notStarted,
  &__inProgress {
    background-position: 0 -26px;
  }
  &__worried {
    background-position: -55px -25px;
  }
  &__won {
    background-position: -82px -25px;
  }
  &__failed {
    background-position: -109px -25px;
  }
}
